import { Button } from "@/components/ui/button"
import { cn } from "@/lib/utils"
import { ChevronLeft, ChevronRight, Lock } from "lucide-react"

interface NavigationButtonsProps {
  currentStep: number
  totalSteps: number
  onBack: () => void
  onNext: () => void
  isSubmitting: boolean
  hasErrors: boolean
}

export function NavigationButtons({
  currentStep,
  totalSteps,
  onBack,
  onNext,
  isSubmitting,
  hasErrors,
}: NavigationButtonsProps) {
  const isLastStep = currentStep === totalSteps - 2

  return (
    <div className="flex justify-between mt-8">
      <Button
        type="button"
        variant="outline"
        onClick={onBack}
        disabled={currentStep === 0 || isSubmitting}
        className={cn(currentStep === 0 && "invisible")}
      >
        <ChevronLeft className="mr-2 h-4 w-4" />
        Voltar
      </Button>

      <Button
        type="button"
        onClick={onNext}
        disabled={hasErrors || isSubmitting}
        className={cn(
          "relative",
          isLastStep && "bg-gradient-to-l to-purple-600 from-blue-600",
          hasErrors && "opacity-50 cursor-not-allowed",
        )}
      >
        {isSubmitting ? (
          <span className="flex items-center">
            <svg
              className="animate-spin -ml-1 mr-2 h-4 w-4 text-white"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
            >
              <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
              <path
                className="opacity-75"
                fill="currentColor"
                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
              ></path>
            </svg>
            Processando
          </span>
        ) : (
          <>
            {isLastStep ? (
              <>
                <div className="absolute w-7 h-7 left-2 top-1/2 -translate-y-1/2 rounded-full hidden group-hover:flex items-center justify-center bg-white/20">
                  <Lock className="opacity-60 h-4 w-4" />
                </div>
                <span>Finalizar Cadastro</span>
              </>
            ) : (
              <>
                <span>Próximo</span>
                <ChevronRight className="ml-2 h-4 w-4" />
              </>
            )}
          </>
        )}
      </Button>
    </div>
  )
}

