"use client"

import { useState } from "react"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import { AnimatePresence } from "framer-motion"
import { Info, Key, Building2 } from "lucide-react"
import { Form } from "@/components/ui/form"
import { type SignUpRequest, signUpSchema } from "@/models/auth.model"
import { PersonalInfoStep } from "./steps/personal-info-step"
import { SecurityStep } from "./steps/security-step"
import { CompanyStep } from "./steps/company-step"
import { CompletionStep } from "./steps/completion-step"
import { NavigationButtons } from "./steps/navigation-buttons"

export function SignUpForm() {
  const [currentStep, setCurrentStep] = useState(0)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submissionStatus, setSubmissionStatus] = useState<"idle" | "success" | "error">("idle")

  const form = useForm<SignUpRequest>({
    mode: "onChange",
    criteriaMode: "all",
    resolver: zodResolver(signUpSchema),
    defaultValues: {
      name: "",
      email: "",
      password: "",
      confirmPassword: "",
      company: "",
    },
  })

  const { trigger, formState } = form
  const { errors } = formState
  const currentStepHasErrors = steps[currentStep].fields.some((field) => errors[field as keyof SignUpRequest])

  
  // Adicione esta nova constante para verificar se os campos estão preenchidos
  const currentStepFieldsComplete = steps[currentStep].fields.every((field) => {
    const value = form.getValues(field as keyof SignUpRequest)
    return value && value.toString().trim() !== ""
  })

  const handleNext = async () => {
    const fieldsToValidate = steps[currentStep].fields as Array<keyof SignUpRequest>

    // Verifica se todos os campos do step atual estão preenchidos
    const isStepValid = await trigger(fieldsToValidate, { shouldFocus: true })

    if (!isStepValid) {
      // Se não estiver válido, não permite avançar
      return
    }

    if (currentStep < steps.length - 2) {
      setCurrentStep((step) => step + 1)
    } else if (currentStep === steps.length - 2) {
      await handleSubmit()
    }
  }

  const handleBack = () => {
    setCurrentStep((step) => step - 1)
  }

  const handleSubmit = async () => {
    const isStepValid = await trigger(steps[currentStep].fields as Array<keyof SignUpRequest>)

    if (!isStepValid) return

    setIsSubmitting(true)

    try {
      // Simulate API call
      await new Promise((resolve) => setTimeout(resolve, 1500))

      const formData = form.getValues()
      console.log("Form submitted successfully:", formData)

      setSubmissionStatus("success")
      setCurrentStep(steps.length - 1)
    } catch (error) {
      console.error("Error submitting form:", error)
      setSubmissionStatus("error")
      setCurrentStep(steps.length - 1)
    } finally {
      setIsSubmitting(false)
    }
  }

  const resetForm = () => {
    setSubmissionStatus("idle")
    setCurrentStep(0)
  }

  return (
    <div className="w-full max-w-md mx-auto">
      <Form {...form}>
        <form className="space-y-6">
          <AnimatePresence mode="wait">
            {currentStep === 0 && <PersonalInfoStep form={form} steps={steps} currentStep={currentStep} />}

            {currentStep === 1 && <SecurityStep form={form} steps={steps} currentStep={currentStep} />}

            {currentStep === 2 && <CompanyStep form={form} steps={steps} currentStep={currentStep} />}

            {currentStep === 3 && <CompletionStep status={submissionStatus} onReset={resetForm} />}
          </AnimatePresence>

          {currentStep < steps.length - 1 && (
            <NavigationButtons
              currentStep={currentStep}
              totalSteps={steps.length}
              onBack={handleBack}
              onNext={handleNext}
              isSubmitting={isSubmitting}
              hasErrors={currentStepHasErrors || !currentStepFieldsComplete}
            />
          )}
        </form>
      </Form>
    </div>
  )
}

